@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

* {
	margin: 0;
	padding: 0;
	outline: none;
	box-sizing: border-box;
}

input {
	font: inherit;
	border: none;
}
a {
	text-decoration: none;
}

button {
	-webkit-appearance: none;
	        appearance: none;
	background-color: transparent;
	border: none;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5 {
	font-weight: 500;
	font-family: "Poppins", sans-serif;
}

.main-container {
	background-color: #f1f2f5;
	padding: 30px 150px;
}

.main-container .body-header {
	position: relative;
	margin-bottom: 50px;
}
.main-container .body-header .icon-wrapper {
	position: absolute;
	top: 0;
	left: -85px;
	transition: 200ms ease-in-out;
	cursor: pointer;
}
.main-container .body-header .icon-wrapper:hover {
	left: -90px;
	transition: 200ms ease-in-out;
}

.main-container .body-header .icon-wrapper svg {
	font-size: 28px;
}

.main-container .body-header h2 {
	margin-bottom: 15px;
}
.main-container .body-header p {
	color: rgb(90, 90, 90);
}

.main-container .card-wrapper {
	background-color: #fff;
	border-radius: 7px;
	box-shadow: 0 0 2px 2px #b9b9b91a;
	padding: 30px 50px;
	margin-bottom: 30px;
}

.main-container .first-card h3 {
	margin-bottom: 35px;
}

.main-container .first-card .services {
	display: flex;
	justify-content: space-between;
}

.main-container .first-card .services .service {
	border: 1px solid #7f858a70;
	border-radius: 5px;
	padding: 15px;
	display: flex;
	align-items: center;
	position: relative;
	height: 85px;
	width: 230px;
	color: #7f858a;
	overflow: hidden;
	transition: 200ms ease-in-out;
	cursor: pointer;
}
.main-container .first-card .services .service.truck svg {
	right: -8px;
	bottom: -17px;
}
.main-container .first-card .services .service svg {
	position: absolute;
	font-size: 80px;
	color: #7f858a;
	bottom: -18px;
	right: -20px;
	transition: 100ms ease-in-out;
}

.main-container .first-card .services .service.active,
.main-container .first-card .services .service:hover {
	background-color: #0846a6;
	border-color: #fff;
	color: #fff;
	transition: 200ms ease-in-out;
}
.main-container .first-card .services .service.active svg,
.main-container .first-card .services .service:hover svg {
	color: #fff;
	transition: 100ms ease-in-out;
}

.main-container .first-icon {
	margin-bottom: 10px;
}
.main-container .port-info-wrapper {
	display: flex;
	justify-content: space-between;
	color: #7f858a;
	margin-bottom: 40px;
}

.main-container .port-info-wrapper .import-export {
	display: flex;
	border: 1px solid #7f858a70;
	border-radius: 3px;
}
.main-container .port-info-wrapper .import-export .import {
	border-right: 1px solid #7f858a70;
}
.main-container .port-info-wrapper .import-export div {
	display: flex;
	align-items: center;
	padding: 12px;
}

.main-container .port-info-wrapper .location,
.main-container .port-info-wrapper .date,
.main-container .port-info-wrapper .dropdown .trigger,
.main-container .port-info-wrapper .total {
	display: flex;
	align-items: center;
	background-color: #f3f3f3;
	padding: 12px 20px;
	width: 370px;
	border-radius: 3px;
}
.main-container .port-info-wrapper svg {
	color: rgb(0, 0, 0);
}
.main-container .port-info-wrapper .location svg,
.main-container .port-info-wrapper .date svg {
	margin-right: 5px;
}
.main-container .port-info-wrapper .location p span,
.main-container .port-info-wrapper .date {
	color: rgb(0, 0, 0);
}

.main-container .port-info-wrapper .date,
.main-container .port-info-wrapper .dropdown .trigger,
.main-container .port-info-wrapper .total {
	width: 300px;
	color: rgb(0, 0, 0);
}
.main-container .port-info-wrapper .dropdown .trigger {
	justify-content: space-between;
	cursor: pointer;
}
.main-container .port-info-wrapper .dropdown {
	position: relative;
}
.main-container .port-info-wrapper .dropdown .items {
	position: absolute;
	width: 100%;
	background-color: #f3f3f3;
	box-shadow: 0 0 4px 4px #b9b9b91a;
	top: 120%;
	border-radius: 3px;
	padding: 12px 20px 0;
	color: rgb(0, 0, 0);
}
.main-container .port-info-wrapper .dropdown .items p {
	margin-bottom: 10px;
}
.card-wrapper .third-card {
	margin-bottom: 30px;
}
.card-wrapper .third-card .card-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 30px;
}
.card-wrapper .third-card .card-header .desc {
	display: flex;
	align-items: center;
}
.card-wrapper .third-card .card-header .desc label {
	margin-right: 10px;
}
.card-wrapper .third-card .card-header .desc p span {
	color: #7f858a;
}

.toggle-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #f3f3f3;
	width: 350px;
	height: 45px;
	position: relative;
	border: 3px solid #f3f3f3;
	border-radius: 5px;
	margin-bottom: 30px;
}
.toggle-header .toggle-bg {
	position: absolute;
	background-color: #fff;
	top: 0;
	left: 0;
	height: 100%;
	width: 50%;
	z-index: 0;
	border-radius: 5px;
	transition: 300ms ease;
	box-shadow: 0 0 2px 2px #b9b9b91a;
}
.toggle-header .toggle-bg.right {
	left: 50%;
	transition: 300ms ease;
}
.toggle-header .items {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	z-index: 1;
}
.toggle-header .items .item {
	padding: 0 20px;
	height: 100%;
	cursor: pointer;
	display: flex;
	align-items: center;
	color: #7f858a;
	font-weight: 500;
	transition: 400ms ease-in-out;
}
.toggle-header .items .item.active {
	color: #0846a6;
	transition: 400ms ease-in-out;
}
.third-card .input-container {
	display: flex;
	align-items: center;
}

.card-wrapper .title {
	margin-bottom: 50px;
}
.card-wrapper .fourth-card {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 65px;
	margin-bottom: 30px;
}
.card-wrapper .fourth-card .item {
	display: flex;
	align-items: baseline;
}
.card-wrapper .fourth-card .item label {
	margin-right: 30px;
}
.card-wrapper .fourth-card .item .desc h3 {
	margin-bottom: 7px;
	font-family: inherit;
}
.card-wrapper .fourth-card .item .desc p {
	color: #7f858a;
	font-size: 15px;
}
/* Toggle Slider */
.switch {
	position: relative;
	display: inline-block;
	width: 55px;
	height: 30px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	transition: 300ms ease;
}

.slider:before {
	position: absolute;
	content: "";
	height: 25px;
	width: 25px;
	left: 2px;
	bottom: 2.6px;
	background-color: white;
	transition: 300ms ease;
}

input:checked + .slider {
	background-color: #0846a6;
}

input:focus + .slider {
	box-shadow: 0 0 1px #0846a6;
}

input:checked + .slider:before {
	-webkit-transform: translateX(25px);
	transform: translateX(25px);
}

/* Rounded sliders */
.slider.round {
	border-radius: 34px;
}

.slider.round:before {
	border-radius: 50%;
}

/* Input */
.input-wrapper {
	position: relative;
	margin-right: 30px;
}
.input-wrapper label {
	position: absolute;
	left: 7px;
	top: 12px;
	padding: 0 5px;
	transition: 200ms ease-in-out;
	color: #7f858a;
	pointer-events: none;
}
.input-wrapper input {
	border: 1px solid #7f858a70;
	padding: 12px 50px 12px 20px;
	border-radius: 5px;
	max-width: 200px;
}
.input-wrapper .unit {
	position: absolute;
	right: 10px;
	top: 0;
	height: 100%;
	color: #7f858a;
	display: flex;
	align-items: center;
}
input:focus ~ label,
input:not(:focus):valid ~ label {
	top: -8px;
	left: 12px;
	font-size: 14px;
	background-color: #fff;
	color: unset;
}

.header {
	background-color: #0846a6;
	color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 30px;
}
.header .logo-search-container {
	display: flex;
	align-items: center;
}

.header .logo-search-container .logo-wrapper {
	width: 80px;
	margin-right: 80px;
}
.header .logo-search-container .logo-wrapper img {
	width: 100%;
}

.header .logo-search-container .search-bar-wrapper {
	position: relative;
}
.header .logo-search-container .search-bar-wrapper input {
	background-color: #386cb7;
	color: #fff;
	padding: 12px 20px;
	width: 400px;
}
.header .logo-search-container .search-bar-wrapper input::-webkit-input-placeholder {
	color: rgba(255, 255, 255, 0.637);
}
.header .logo-search-container .search-bar-wrapper input:-ms-input-placeholder {
	color: rgba(255, 255, 255, 0.637);
}
.header .logo-search-container .search-bar-wrapper input::placeholder {
	color: rgba(255, 255, 255, 0.637);
}
.header .logo-search-container .search-bar-wrapper .search-icon {
	position: absolute;
	height: 100%;
	top: 0;
	right: 0;
	padding-right: 20px;
	display: flex;
	align-items: center;
}
.header .cta-buttons-wrapper a {
	padding: 10px 20px;
	color: #fff;
	font-weight: 500;
	border: 2px solid;
	border-radius: 5px;
	transition: 200ms ease-in-out;
	font-family: "Poppins", sans-serif;
}
.header .cta-buttons-wrapper .request {
	border-color: rgba(255, 255, 255, 0.637);
	margin-right: 40px;
}
.header .cta-buttons-wrapper .request:hover {
	transition: 200ms ease-in-out;
	border-color: rgba(255, 255, 255, 0.877);
}
.header .cta-buttons-wrapper .book {
	border-color: #5bc785;
	margin-right: 40px;
	background-color: #5bc785;
}
.header .cta-buttons-wrapper .book:hover {
	background-color: #26c061;
	transition: 200ms ease-in-out;
	border-color: #26c061;
}

